import { FC } from 'react';
import classNames from 'classnames';

// Components
import Link, { LinkProps } from '../Link';

// Types
import { TestMetadata } from 'types';

// Styles
import './Breadcrumb.scss';

export interface BreadcrumbBaseProps {
  // Class name to use on container
  className?: string;
  // Test IDs for sections of Breadcrumbs
  testMetadata?: TestMetadata;
}

export type BreadcrumbLinkProps =
  | {
      // The HREF attribute of the anchor tag.
      href: string;
      // Define the underline styles for the link component when `href` prop is set.
      underline?: LinkProps['underline'];
    }
  | {
      href?: string;
      underline?: never;
    };

export type BreadcrumbProps = BreadcrumbBaseProps & BreadcrumbLinkProps;

const Breadcrumb: FC<BreadcrumbProps> = ({
  className,
  testMetadata,
  children,
  href,
  underline,
}) => {
  if (href) {
    return (
      <Link
        testMetadata={testMetadata}
        underline={underline}
        href={href}
        className={classNames('sb-breadcrumb sb-breadcrumb__link', className)}
      >
        {children}
      </Link>
    );
  }

  return (
    <span className={classNames('sb-breadcrumb', className)} {...testMetadata}>
      {children}
    </span>
  );
};

export default Breadcrumb;
