import { ComponentProps, FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './Link.scss';

// Types
import { TestMetadata } from 'types';

export interface LinkProps extends ComponentProps<'a'> {
  /** Elements to Wrap */
  children?: ReactNode;
  /** Custom class name(s) for component container */
  className?: string;
  /** Disables the Link */
  disabled?: boolean;
  /** Address of the Link */
  href?: string;
  /**
   * Relationship between the location in the document containing the hyperlink and the destination resource.
   * Omitted if the href attribute is not present.
   * Default is "noopener noreferrer" for security reasons: https://web.dev/external-anchors-use-rel-noopener/
   */
  rel?: string;
  /** Text underline decoration*/
  underline?: 'none' | 'hover' | 'always';
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Link: FC<LinkProps> = ({
  children,
  className,
  disabled,
  href,
  testMetadata,
  rel = 'noopener noreferrer',
  underline = 'none',
  ...otherProps
}) => {
  const buttonGroupClassNames = useMemo(
    () =>
      classNames(
        'sb-link',
        `sb-link_underline_${underline}`,
        {
          'sb-link_disabled': disabled,
        },
        className,
      ),
    [className, underline, disabled],
  );

  return (
    <a
      aria-disabled={disabled}
      className={buttonGroupClassNames}
      href={href}
      rel={href && rel}
      {...testMetadata}
      {...otherProps}
    >
      {children}
    </a>
  );
};

export default Link;
